import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { SpaceModel } from '../../models/space.model';
import { SpaceService } from '../../services/space.service';
import { CategoryService } from '../../services/category.service';
import { SpaceTagsService } from '../../services/space_tags.service';
import { CityService } from '../../services/city.service';
declare var $: any;
declare var google;

@Component({
    selector: 'app-search',
    templateUrl: './search.component.html',
    styleUrls: ['./search.component.css']
})

export class SearchComponent implements OnInit {
    categoryFilter = '';
    cityFilter = '';
    capacityFilter: number;
    minPriceFilter: number;
    maxPriceFilter: number;
    lastCategoryId = '';
    listSpace: SpaceModel[] = [];
    totalSpace = 0;
    atualPage = 1;
    totalPage: any = [];
    listCategory: any = [];
    allCategories: any = [];
    idCategory = '';
    statusModal = 'close';
    statusModalCity = 'close';
    emptyStatusCategory = 'close';
    emptyStatusCity = 'close';
    isLogin = false;
    space: SpaceModel = new SpaceModel();
    spaceTags: any[] = [];
    city: any = [];
    allCity: any = [];
    canSee = false;

    constructor(
        private cityService: CityService,
        private categoryService: CategoryService,
        private spaceService: SpaceService,
        private route: ActivatedRoute,
        private router: Router,
        private spaceTagsService: SpaceTagsService
    ) {
        $(document).ready(function () {
            $('.navbar-toggler').click(function (e) {
                e.preventDefault();
                if ($('.navbar-collapse').hasClass('opened')) {
                    $('.navbar-collapse').removeClass('opened');
                    $('.nav-backdrop').removeClass('show');
                } else {
                    $('.navbar-collapse').addClass('opened');
                    $('.nav-backdrop').addClass('show');
                }
            });
            if ($(window).width() < 992) {
                $('#select-filter').selectpicker({
                    dropupAuto: false
                });
                $('#select-filter').parent().find('div.dropdown-menu').addClass('show');
                $('#select-filter').selectpicker('toggle');
            }
            $('.btn-map').click(function (e) {
                e.preventDefault();
                if ($('.content-map .map').hasClass('active')) {
                    $('.btn-map').find('span').text('Mapa');
                    $('.btn-map').find('i').removeClass('fa-list').addClass('fa-map-marker');
                } else {
                    $('.btn-map').find('span').text('Lista');
                    $('.btn-map').find('i').removeClass('fa-map-marker').addClass('fa-list');
                }
                $('.content-map .map').toggleClass('active');
            });
            $('.btn-filter').click(function (e) {
                e.preventDefault();
                $('section.filters').toggleClass('active');
            });
            $('.btn-close-filters').click(function (e) {
                e.preventDefault();
                $('section.filters').toggleClass('active');
            });
            $('.cancel-filter').click(function (e) {
                e.preventDefault();
                $('section.filters').toggleClass('active');
            });
        });
    }

    async ngOnInit() {
        await this.allCategory();
        await this.urlParams();
        const query = this.mountQuery();
        await this.getAllSpaceTags(query);
        const cities = await this.cityService.getAllCities();
        this.city = cities;
        this.allCity = cities;
        this.canSee = true;
    }

    mountQuery(map?): string {
        this.allCategories.forEach(cat => {
            if (cat.name === this.categoryFilter) { this.categoryFilter = cat.id; }
        });
        this.lastCategoryId = this.categoryFilter;
        const filters = document.querySelectorAll('input[name="filter"]');
        const filtersId = [];
        for (let i = 0; i < filters.length; i++) {
            if ((<HTMLInputElement>filters[i]).checked === true) {
                filtersId.push((<HTMLInputElement>filters[i]).value);
            }
        }
        const urlCategory = `${this.categoryFilter !== null ? `&category=${this.categoryFilter}` : ''}`;
        const urlCapacity = `${this.capacityFilter ? `&capacity=${this.capacityFilter}` : ''}`;
        const urlMin = `${this.minPriceFilter ? `&min_price=${this.minPriceFilter.toString()}` : ''}`;
        const urlMax = `${this.maxPriceFilter ? `&max_price=${this.maxPriceFilter.toString()}` : ''}`;
        const urlTag = `${filtersId.length !== 0 ? `&tags=${filtersId.toString()}` : ''}`;

        if (map) {
            const bounds = map.getBounds();
            const ne = bounds.getNorthEast();
            const sw = bounds.getSouthWest();
            // tslint:disable-next-line:max-line-length
            const query = `page=${this.atualPage}&per_page=6${urlCategory}${urlCapacity}${urlMin}${urlMax}${urlTag}&point1=${ne.lat()},${sw.lng()}&point2=${sw.lat()},${ne.lng()}`;
            return query;
        } else {
            let query = `page=${this.atualPage}&per_page=6${urlCategory}${urlCapacity}${urlMin}${urlMax}${urlTag}`;
            if (this.cityFilter) {
                const urlCity = `&city=${this.cityFilter}`;
                query = query + urlCity;
            }
            return query;
        }
    }

    async searchSpaces(query: string, firstSearch: boolean) {
        try {
            const res: any = await this.spaceService.getAllPaginated(query);
            this.totalPage = [];
            this.listSpace = [];
            if (res.spaces) {
                this.mountUrl(query, res, firstSearch);
            }
            return res;
        } catch (e) {
            console.log(e);
        }
    }

    mountUrl(query, res, firstSearch) {
        this.totalSpace = res.total_items;
        this.pagination(res.total_items);
        const category = this.allCategories.find(c => c.id === this.lastCategoryId);
        this.categoryFilter = category.name;
        if (!firstSearch) { window.history.pushState('respace', 'respace', `#/search/${category.slug}?${query.replace(`category=${this.lastCategoryId}`, '')}`) }
    }

    async getAllSpaceTags(query: string) {
        try {
            this.route.queryParams.subscribe(async (params: any) => {
                const tags = await this.spaceTagsService.getAll(query);
                this.spaceTags = tags;
                if (params['tags']) {
                    this.writeTags(params['tags']);
                }
            });
        } catch (e) { }
    }

    findMatches(wordToMatch, category) {
        return category.filter(item => {
            const regex = new RegExp(wordToMatch, 'gi');
            return item.name.match(regex);
        });
    }

    toogleFieldsInput(e, type: string) {
        e.target.parentElement.classList.add('show');
        this.returnToggledFields(e.target.id);
        if (type === 'category') {
            this.listCategory = this.findMatches(e.target.value, this.allCategories);
        } else { this.city = this.findMatches(e.target.value, this.allCity); }
    }

    toogleFields(e) {
        const elementHtml = e.target;
        this.returnToggledFields(elementHtml.id);
        if (elementHtml.classList.contains('show')) {
            elementHtml.classList.remove('show');
        } else {
            elementHtml.classList.add('show');
        }
    }

    returnToggledFields(id) {
        const category = document.getElementById('categoryField');
        const city = document.getElementById('cityField');
        const capacity = document.getElementById('capacityField');
        const price = document.getElementById('priceField');
        const more = document.getElementById('moreField');

        switch (id) {
            case 'categoryField':
                city.classList.remove('show');
                capacity.classList.remove('show');
                price.classList.remove('show');
                more.classList.remove('show');
                break;
            case 'cityField':
                category.classList.remove('show');
                capacity.classList.remove('show');
                price.classList.remove('show');
                more.classList.remove('show');
                break;
            case 'capacityField':
                category.classList.remove('show');
                city.classList.remove('show');
                price.classList.remove('show');
                more.classList.remove('show');
                break;
            case 'priceField':
                category.classList.remove('show');
                city.classList.remove('show');
                capacity.classList.remove('show');
                more.classList.remove('show');
                break;
            case 'moreField':
                category.classList.remove('show');
                city.classList.remove('show');
                capacity.classList.remove('show');
                price.classList.remove('show');
                break;
            case 'input-category-search':
                city.classList.remove('show');
                capacity.classList.remove('show');
                price.classList.remove('show');
                more.classList.remove('show');
                break;
            case 'input-city-search':
                category.classList.remove('show');
                capacity.classList.remove('show');
                price.classList.remove('show');
                more.classList.remove('show');
                break;

            default:
                break;
        }
    }

    change(e, type: string) {
        e.target.parentElement.setAttribute('data-value', e.target.value);
        if (type === 'city') {
            const result = this.findMatches(e.target.value, this.allCity);
            result.length === 0 ? this.emptyStatusCity = 'active' : this.emptyStatusCity = 'close';
            this.city = result;
        } else {
            const result = this.findMatches(e.target.value, this.allCategories);
            result.length === 0 ? this.emptyStatusCategory = 'active' : this.emptyStatusCategory = 'close';
            this.listCategory = result;
        }
    }

    selectModal(item: any, type: string) {
        if (type === 'city') {
            { this.cityFilter = item.name; }
        } else { this.categoryFilter = item.name; }
        this.closeModal(type);
    }

    handleOpenModal(e, type: string) {
        window.scrollTo(0, 0);
        const visor = e.target.parentElement.parentElement.querySelector('.form-visor');
        visor.classList.remove('show');
        if (type === 'city') {
            this.statusModalCity = 'open';
        } else { this.statusModal = 'open'; }
    }

    closeModal(type: string) {
        if (type === 'city') {
            this.statusModalCity = 'close';
        } else { this.statusModal = 'close'; }
    }

    writePinImage(items, id) {
        const liImage = document.createElement('ul');
        for (let i = 0; i < items.length; i++) {
            const html =
                `<li>
                <a href='/#/details/${id}'><img src='${items[i].url}' width='100%' class='img-fluid' alt=''></a>
            </li>`;
            liImage.insertAdjacentHTML('beforeend', html);
        }
        return liImage.innerHTML;
    }

    writePin(item, e) {
        const htmlModalPin = document.querySelector('[data-js="mapPopUp"]');
        const pinHtml = `<ul class='pin__image'>
                ${this.writePinImage(item.contentImages, item.contentId)}
            </ul>
            <div class='pin__info'>
                <p class='pin__price'>${this.price(item.contentPrice)}</p>
                <h2 class='pin__title'>${item.contentTitle}</h2>
                <p class='pin__text'>
                    <span class='pin__text-person'><img src='assets/images/user-icon.png' alt=''> Capacidade: ${item.contentCapacity} pessoas</span>
                </p>
        </div>`;
        htmlModalPin.innerHTML = pinHtml;
        htmlModalPin.setAttribute('style', `display: inline-block; transform: translate(calc(${e.wa.pageX}px - 242px), calc(${e.wa.pageY}px - 371px))`)
        $('[data-js="mapPopUp"] ul').slick({
            arrows: true,
            dots: false,
            infinite: true,
            slidesToShow: 1,
            slidesToScroll: 1,
            prevArrow: '<a href="javascript:void(0);" class="custom-arrow slick-prev"><img src="assets/images/left-arrow.png"/></a>',
            nextArrow: '<a href="javascript:void(0);" class="custom-arrow slick-next"><img src="assets/images/right-arrow.png" /></a>',
        });
    }

    getMoveMap(items, map, htmlModalPin) {
        if (items.length > 0) {
            const myLatLng = [];
            this.listSpace = items;
            this.addImages();
            items.forEach((item) => {
                myLatLng.push({
                    lat: parseFloat(item.address.latitude),
                    lng: parseFloat(item.address.longitude),
                    id: item.id,
                    title: item.name,
                    price: item.price,
                    capacity: item.capacity,
                    images: item.space_images
                });
            });
            myLatLng.forEach(item => {
                const marker = new google.maps.Marker({
                    position: { lat: item.lat, lng: item.lng },
                    map: map,
                    contentId: item.id,
                    contentTitle: item.title,
                    contentPrice: item.price,
                    contentCapacity: item.capacity,
                    contentImages: item.images
                });
                google.maps.event.addListener(marker, 'click', (e) => {
                    htmlModalPin.setAttribute('style', `display: none;`);
                    htmlModalPin.innerHTML = '';
                    this.writePin(marker, e);
                });
            });
        }
    }

    async getMap(items, zoomNumber) {
        this.listSpace = [];
        if (items.length > 0) {
            this.listSpace = items;
            this.addImages();
            const map = new google.maps.Map(document.getElementById('mapResult'), {
                zoom: zoomNumber,
                center: { lat: parseFloat(items[0].address.latitude), lng: parseFloat(items[0].address.longitude) }
            });
            const htmlModalPin = document.querySelector('[data-js="mapPopUp"]');

            const myLatLng = [];
            items.forEach((item) => {
                myLatLng.push({
                    lat: parseFloat(item.address.latitude),
                    lng: parseFloat(item.address.longitude),
                    id: item.id,
                    title: item.name,
                    price: item.price,
                    capacity: item.capacity,
                    images: item.space_images
                });
            });
            myLatLng.forEach(item => {
                const marker = new google.maps.Marker({
                    position: { lat: item.lat, lng: item.lng },
                    map: map,
                    contentId: item.id,
                    contentTitle: item.title,
                    contentPrice: item.price,
                    contentCapacity: item.capacity,
                    contentImages: item.images
                });
                google.maps.event.addListener(marker, 'click', (e) => {
                    htmlModalPin.setAttribute('style', `display: none;`);
                    htmlModalPin.innerHTML = '';
                    this.writePin(marker, e);
                });
            });
            map.addListener('drag', function (event) {
                htmlModalPin.setAttribute('style', `display: none;`);
                htmlModalPin.innerHTML = '';
            });
            google.maps.event.addListener(map, 'dragend', async (e) => {
                const query = this.mountQuery(map);
                const res = await this.searchSpaces(query, false);
                this.getMoveMap(res.spaces, map, htmlModalPin);
                await this.getAllSpaceTags(query);
            });
        } else {
            const map = new google.maps.Map(document.getElementById('mapResult'), {
                zoom: 7,
                center: { lat: -23.6815314, lng: -46.8754974 }
            });
        }
    }

    pagination(num: number) {
        const pages = Math.ceil(num / 6);
        for (let i = 0; i < pages; i++) {
            this.totalPage.push(i + 1);
        }
    }

    goToDetails(item: SpaceModel) {
        this.router.navigateByUrl(`/details/${item.id}`);
    }

    activePage(item: number) {
        const links = document.querySelectorAll('.pagination a');
        const linkActual = document.querySelector(`[data-page='${item}']`);
        for (let i = 0; i < links.length; i++) {
            links[i].classList.remove('active');
        }
        linkActual.classList.add('active');
    }

    async clickPage(item: number) {
        if (item !== this.atualPage) {
            this.atualPage = item;
            const query = this.mountQuery();
            this.activePage(item);
            const res = await this.searchSpaces(query, false);
            this.getMap(res.spaces, 7);
        }
    }

    async clickPageNext() {
        this.atualPage++;
        const query = this.mountQuery();
        this.activePage(this.atualPage);
        const res = await this.searchSpaces(query, false);
        this.getMap(res.spaces, 7);
    }

    async clickPagePrev() {
        this.atualPage--;
        const query = this.mountQuery();
        this.activePage(this.atualPage);
        const res = await this.searchSpaces(query, false);
        this.getMap(res.spaces, 7);
    }

    writeTags(item) {
        const newItem = item.split(',');
        const allList = document.querySelectorAll('.form-list-filter li');
        for (let i = 0; i < newItem.length; i++) {
            for (let j = 0; j < allList.length; j++) {
                if (allList[i].querySelector('input').value === newItem[i]) {
                    allList[i].querySelector('input').checked = true;
                }
            }

        }
    }

    async urlParams() {
        let urlSpace = '';

        this.route.params.subscribe(async params => {
            const categorySlug = params['category_slug'];
            const category = this.allCategories.find(item => item.slug === categorySlug);
            this.idCategory = category ? category.id : this.idCategory;
            this.categoryFilter = category ? category.name : this.categoryFilter;
            urlSpace = `${urlSpace}category=${this.idCategory}`;
        });

        this.route.queryParams.subscribe(async (params: any) => {
            Object.keys(params).map(async (item, index) => {
                const param = params[item];
                if (item === 'city') {
                    this.cityFilter = param;
                }
                if (item === 'capacity') {
                    this.capacityFilter = param;
                }
                if (item === 'min_price') {
                    this.minPriceFilter = param;
                }
                if (item === 'max_price') {
                    this.maxPriceFilter = param;
                }
                if (item === 'tags') {
                    this.writeTags(param);
                }
                urlSpace = `${urlSpace}&${item}=${param}`;
            });

            this.totalPage = [];
            const res = await this.searchSpaces(urlSpace, true);
            this.getMap(res.spaces, 7);
        });
    }

    price(value) {
        const valueFormat = value.toFixed(2).split('.');
        valueFormat[0] = `R$ ${valueFormat[0].split(/(?=(?:...)*$)/).join('.')}`;
        return ` ${valueFormat.join(',')}/hr`;
    }

    async handleFilter(e) {
        this.totalPage = [];
        document.querySelector('[data-js="mapPopUp"]').setAttribute('style', 'display: none');
        const allElement = document.querySelectorAll('.form-visor');
        for (let i = 0; i < allElement.length; i++) {
            allElement[i].classList.remove('show');
        }
        const query = this.mountQuery();
        await this.getAllSpaceTags(query);
        const res = await this.searchSpaces(query, false);
        this.getMap(res.spaces, 7);
    }

    handleClick(e, item: any, type: string) {
        const elementVisor = e.target.parentElement.parentElement.firstChild;
        elementVisor.classList.remove('show');
        if (type === 'category') {
            this.categoryFilter = item.name;
        } else { this.cityFilter = item.name; }
    }

    async allCategory() {
        try {
            this.listCategory = await this.categoryService.getAllCategories();
            this.allCategories = await this.categoryService.getAllCategories();
            const textCategory = this.listCategory.filter(item => {
                if (item.id === parseInt(this.idCategory)) {
                    return item;
                }
            });
            if (textCategory.length > 0) { this.categoryFilter = textCategory; }
        } catch (e) {
            console.log(e);
        }
    }

    async favoriteSpace(space) {
        this.space = space;
        if (this.spaceService.getUserInfo()) {
            const userInfo = this.spaceService.getUserInfo();
            const userFavoriteModel = {
                space_id: undefined,
                user_id: undefined
            };
            userFavoriteModel.space_id = this.space.id;
            userFavoriteModel.user_id = userInfo.id;

            try {
                let favorite;
                if (this.space.liked) {
                    favorite = await this.spaceService.deleteFavoriteSpace(userFavoriteModel);
                } else {
                    favorite = await this.spaceService.favoriteSpace(userFavoriteModel);
                }
                const spaceRes = await this.spaceService.getById({ id: this.space.id, user_id: userInfo.id });
                this.space.liked = spaceRes.liked;
            } catch (e) {
                console.log(e);
            }

        } else {
            this.loginModal();
        }
    }

    async loginModal() {
        this.isLogin = !this.isLogin;
        const userInfo = this.spaceService.getUserInfo();
        try {
            if (this.spaceService.getUserInfo()) {
                const favoriteSpace = await this.favoriteSpace(this.space);
            }
            if (userInfo) {
                const space = await this.spaceService.getById({ id: this.space.id, user_id: userInfo.id });
                this.space.liked = space.liked;
                this.urlParams();
            }
        } catch (e) {
            console.log(e);
        }
    }

    addImages() {
        this.listSpace.forEach(space => {
            space.images = [];
            space.space_images.forEach(image => {
                if (image.main) {
                    space.images.unshift({ url: image.url, clickAction: () => this.router.navigate(['/details', space.id]) });
                } else { space.images.push({ url: image.url, clickAction: () => this.router.navigate(['/details', space.id]) }); }
            });
        });
    }
}
